                margin: 0 auto;
                position: relative;
                background: -moz-linear-gradient(center top, #fff 5%, #fefefe 100%);
                background: -ms-linear-gradient(top, #fff 5%, #fefefe 100%);
                filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#fefefe');
                background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #fff), color-stop(100%, #fefefe));
                background-color: #fefefe;
                color: #000;
                text-shadow: 1px 1px 0 #fff;
                border: 1px solid #bbbeb6;
                padding: 5px 10px;
                -webkit-border-radius: 10px;
                -moz-border-radius: 10px;
                border-radius: 10px;
                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    z-index: 2;
                    top: 100%;
                    left: 15px;
                    width: 0;
                    height: 0;
                    margin-top: -1px;
                    border-left: 15px solid transparent;
                    border-right: 15px solid transparent;
                    border-top: 15px solid #fff;
                }
                &:after {
                    content: '';
                    display: block;
                    position: absolute;
                    z-index: 1;
                    top: 100%;
                    left: 15px;
                    width: 0;
                    height: 0;
                    border-left: 15px solid transparent;
                    border-right: 15px solid transparent;
                    border-top: 15px solid #bbbeb6;
                }
            }
            .review-text {
                padding: 5px 5px 0;
