    color:#46670F;
    text-align: center;
    font-weight:bold;
    font-size: 20px;
    a,span {
        margin: 0 3px 0 3px;
    }
    a {
        color:#46670F;
        font-size:16px;
        text-decoration: none;
    }
    span{
        color:black;
    }
}
/*------------------------------------*\
    WORDPRESS CORE
\*------------------------------------*/
.alignnone {
  margin: 5px;
}
.aligncenter,
div.aligncenter {
  display: block;
  margin: 5px auto;
}
.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}
.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}
.wp-caption {
  background: #fff;
  border: 1px solid #f0f0f0;
  max-width: 96%;
  padding: 5px 3px 10px;
  text-align: center;
  img {
    border: 0 none;
    height: auto;
    margin: 0;
    max-width: 98.5%;
    padding: 0;
    width: auto;
  }
  .wp-caption-text {
    font-size: 80%;
    line-height: 17px;
    margin: 0;
    padding: 0 4px 5px;
  }
}
.gallery-caption {
  font-size: 80%;
  line-height: 17px;
  margin: 0;
  padding: 0 4px 5px;
}
.inc-img-200-200 {
    img {
        width: 200px;
        height: 200px;
    }
}
.page-texture-child-limba {
    .work-image-container {
        width: 250px;
        height: 160px;
        display: block;
        img {
            width: 100%;
            height: 100%;
        }
    }
    .text-center {
        padding: 5px 0;
        a{
            text-decoration: none;
        }
    }
}
.gallery-size-thumbnail {
    br {
        display: none;
    }
}
.product-single-page {
    h1 {
        width: 100%;
        float: left;
    }
    .first-imager {
        width: 250px;
        height: 340px;
        float: left;
        margin: 0 20px 20px 0;
        img {
            width: 250px;
            height: 250px;
            margin: 0 0 10px;
        }
    }
    .buy-btn {
        width: 150px;
        height: 30px;
        margin: 0 50px 10px;
        background-color: green;
        background: linear-gradient(to bottom, #afc49f 50%, #37680e 100%);
        transition: all 1s;
        text-align: center;
        display: block;
        text-decoration: none;
        color: #fff;
        font-size: 20px;
        border-radius: 4px;
        &:hover {
            background: linear-gradient(to bottom, #66c21a 50%, #7b9d60 100%);
        }
    }
    .price {
        display: block;
        height: 30px;
        margin: 0 10px 10px;
        text-align: center;
        img {
            height: 15px;
            width: 12px;
            margin: 0 0 0 5px;
        }
    }
}
.product-gallery {
    a {
        margin: 0 5px 5px;
        border: 3px solid #afc49f;
    }
}